:root {
  --bg-main: #EDEDED;
  --bg-nav: #8C0060;
  --bg-dark: #03071e;
  --bg-white: #f4f4f4;
  --bg-gray: #CDCDCD;
  --bg-gray-2: #ADADAD;
  --bg-yellow: #FBFB00;
  --bg-darkyellow: #E1CD00;
  --bg-orange: #F4B100;
  --bg-green: #319966;
  --bg-darkpurple: #68005B;
  --bg-red: #DF2E2E;
  --bg-darkred: #87002D;
  --bg-violet: #B40057;
  --bg-darkrose: #b41344;
  --bg-lightblue: #05C4CD;
}

#root {
  height: 100%;
  font-family: 'Lato', sans-serif;
  background: #A770EF;
  background-color: var(--bg-main);
  background: -webkit-linear-gradient(to right, #FDB99B, #CF8BF3, #A770EF);
  background: linear-gradient(to right, #FDB99B, #CF8BF3, #A770EF);
}

/* ----- Scrollbar ----- */

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background-color: var(--bg-gray);
}

::-webkit-scrollbar-thumb {
  background-color: var(--bg-violet);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--bg-violet);
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

p {
  line-height: 1.4;
  font-family: 'Noto Sans', system-ui, sans-serif;
}

.text-decoration-none {
  text-decoration: none;
}

.pointer:hover {
  cursor: pointer;
}

/*------------- Efecto de fondo en iconos -------------*/
.icon-effect {
  display: table;
}

.icon-effect:hover {
  position: relative;
  cursor: pointer;
}

.icon-effect:hover::before {
  content: '';
  position: absolute;
  z-index: 99999999999;
  background-color: rgba(0, 0, 0, .15);
  top: -.45em;
  left: -.75em;
  bottom: -.45em;
  right: -.65em;
  border-radius: 50%;
}

.icon-effect.radius-sm:hover::before {
  border-radius: 7px;
}

.icon-effect.white:hover::before {
  background-color: rgba(255, 255, 255, .06);
}

.d-flex {
  display: flex;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.j-center {
  justify-content: center;
}

.j-around {
  justify-content: space-around;
}

.align-items-center {
  align-items: center;
}

.border-none {
  border: none;
  outline: none;
}

.checkbox {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: table;
  font-size: 1em;
  padding: 3px 0;
  font-family: "Titillium Web", sans-serif;
}

.checkbox > .title:before {
  content: '';
  display: inline-flex;
  width: 15px;
  height: 15px;
  padding: 8.5px;
  border-radius: 3px;
  font-size: 11px;
  background-color: var(--bg-gray);
  margin-right: 7px;
}

.checkbox.active > .title:before {
  content: '\f00c';
  font-weight: 900;
  padding: 0 3px;
  font-family: 'Font Awesome 5 Free';
  display: table;
  background-color: var(--bg-darkrose);
  color: var(--bg-white)
}